@import url(https://fonts.googleapis.com/css?family=Roboto:400,700&display=swap);
.menu{
  width: 100%;
  height: 70px;

  display: flex;
  justify-content: space-between;
  align-items: center;
}

.list-menu li{list-style: none; display: inline-block; padding: 10px 10px; margin:5px;}
.list-menu li a{font-weight: bold; text-decoration: none; color:#fff;}
.list-menu li:nth-child(3) a{
  color:#000;
  padding: 7px 30px;
  background: #E6B141;
  border-radius: 50px;
}

.slide-home {height: 500px; margin:100px auto;}
.desc-slide-home strong {width: 500px; color:#fff; font-size: 50px; margin-top:70px;}
.desc-slide-home strong b {color:#E6B141;}
.desc-slide-home {display: grid; grid-template-columns: 1fr 1fr; grid-gap: 20px; gap: 20px;}
.slide-home::after {content: ""; width: 35px; height: 400px; background: url(/static/media/icon-mouse-scroll.76132631.svg) no-repeat center center;
  display: block; margin:-320px auto;}

.stacks{width:35%; margin-right: 10px;}
.stack-developer{height: 180px; background:#1E1E20; border:2px solid #E6B141; border-radius: 10px;
  margin-top:50px; display: flex; justify-content: space-between; align-items: center;}
.stack-developer img{margin:0 20px;}
.stack-developer strong {font-size:1em; font-weight: bold; color: #fff;}
.stack-developer p {font-size: .8em; color:#fff; margin-top:10px; line-height: 20px;}

.aboutus {min-height: 500px; margin:100px auto;}
.desc-aboutus strong {width: 500px; color:#fff; font-size: 50px; margin-top:70px;}
.desc-aboutus strong b {color:#E6B141;}
.desc-aboutus p {width: 50%; color:#fff; line-height: 25px; }
.desc-aboutus {display: grid; grid-template-columns: 1fr; grid-gap: 20px; gap: 20px;}

.infos-aboutus{
  display: grid;
  grid-template-columns: 1fr 1fr;

  justify-content: center;
  align-items: center;
}

.infos-desc p{width: 75%; margin-top:20px; line-height: 25px; font-size: 1.3em;}

.btns-stacks {
  display: flex;
}

.team{min-height: 600px;}
.team strong {width: 500px; color:#fff; font-size: 50px; margin-top:70px;}
.team strong b {color:#E6B141;}
.cardsTeam {display: flex; flex-wrap: wrap; justify-content: space-between;}

.cards {width: 33%; height: 180px; margin-top:10px; display: flex;
  justify-content: center; align-items: center; -webkit-transition: all .3s ease-in-out; transition: all .3s ease-in-out; cursor: pointer;}

.cards:hover{background: #1E1E20; border: 2px solid #E6B141; border-radius: 10px; -webkit-transform: scale(1.1); transform: scale(1.1);}

.info-devs{position:relative; z-index: 1;}
.info-devs p {font-size: .8em; font-weight: bold; color:#E6B141; margin-bottom: 5px;}
.info-devs strong {font-size: 1.2em; flex-wrap: wrap; background: #E6B141; padding:3px 7px; border-radius: 3px;}
.profile-devs {position: relative; overflow: hidden; margin-left: -30px; width: 150px; height: 150px; border-radius: 50%;}

*{margin:0; padding:0; box-sizing:border-box;}

html {
  scroll-behavior: smooth;
}

body{
  background:#17171A;
  font-family: Roboto, sans-serif;
  -webkit-font-smoothing: antialiased;
}

.App {
  width: 1200px;
  margin: 0 auto;
}

::-webkit-scrollbar-track {
  background-color: #17171a;
}
::-webkit-scrollbar {
  width: 6px;
  background: #17171a;
}
::-webkit-scrollbar-thumb {
  background: #E6B141;
  border-radius: 50px;
}

